<template>
    <v-dialog v-model="moreInfoDialog" width="500">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="rgba(0, 0, 0, .7)" block text v-bind="attrs" v-on="on">
          More Details
        </v-btn>
      </template>
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Additional Information
        </v-card-title>
        <v-card-text>
          <v-row>
            <v-col style="height: 200px" class="mt-2">
              <v-card elevation="2" style="height: 150px" color="light-blue lighten-3">
                <v-card-text class="d-flex justify-space-between align-center">
                  <div>
                    <span class="d-flex justify-space-between align-center"
                      >FIT3199</span
                    ><br />
                    <strong style="font-size: 40px; justify-content: center">{{
                      this.activityLog[0].additional.unit1
                    }}</strong>
                  </div>
                </v-card-text>
                <v-card-actions
                  class="d-flex justify-space-between"
                ></v-card-actions>
              </v-card>
            </v-col>
            <v-col style="height: 200px" class="mt-2">
              <v-card elevation="2" style="height: 150px" color="light-blue lighten-3">
                <v-card-text class="d-flex justify-space-between align-center">
                  <div>
                    <span class="d-flex justify-space-between align-center"
                      >
                      ENG0002</span
                    ><br />
                    <strong style="font-size: 40px; justify-content: center">{{
                      this.activityLog[0].additional.unit2
                    }}</strong>
                  </div>
                </v-card-text>
                <v-card-actions
                  class="d-flex justify-space-between"
                ></v-card-actions>
              </v-card>
            </v-col>
            <v-col style="height: 200px" class="mt-2">
              <v-card elevation="2" style="height: 150px" color="red lighten-3">
                <v-card-text class="d-flex justify-space-between align-center">
                  <div>
                    <span class="d-flex justify-space-between align-center"
                      >Pending Jobs</span
                    ><br />
                    <strong style="font-size: 40px; justify-content: center">{{
                      this.activityLog[0].additional.unit2
                    }}</strong>
                  </div>
                </v-card-text>
                <v-card-actions
                  class="d-flex justify-space-between"
                ></v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-card-text>
  
        <v-divider></v-divider>
  
        <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="moreInfoDialog = false">
                      Close
                    </v-btn>
                  </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  <script>
  import { axiosConfig } from "../../axiosConfig.js";
  import store from "../../store.js";
  export default {
    data: () => ({
      activityLog: [
        {
          // Add Document related attributes here
          additional: {
            total: 0,
            // students who found an internship will be implemented later
            unit1: 0,
            unit2: 0,
            pending: 0,
          },
        },
      ],
      moreInfoDialog: false,
    }),
    methods: {
      loadData(axiosInstance) {
        let pending = 8;
        let unit1 = 5;
        let unit2 = 3;
        let self = this;
        self.activityLog[0].additional.unit1 = unit1;
        self.activityLog[0].additional.unit2 = unit2;
        self.activityLog[0].additional.pending = pending;

        axiosInstance
        .get(process.env.VUE_APP_BACKEND_LINK+"admin/app")
        .then(function (response) {
          response
        })
        .catch(function (error) {
          console.log(error);
        });
      },
    },
    mounted() {
      let axiosInstance = axiosConfig(
        store.state.user.token,
        store.state.user.role.toLowerCase()
      );
      this.loadData(axiosInstance);
    },
  };
  </script>