<template>
    <v-container fluid>
        <v-card>
            <v-card-title>Help page</v-card-title>
            <span class="text-left">
                <v-card-text>The place to get help for usage of the application</v-card-text>
            </span>
        </v-card>

        <v-card>
            <v-card-title>
                Documentation
            </v-card-title>
            <span class="text-left">
                <v-card-text>Click on the button below to access the user guide</v-card-text>
            </span>


            <v-col class="text-left">
                <v-btn color="primary" @click="openUserGuide()">
                    User guide
                </v-btn>
            </v-col>
        </v-card>
    </v-container>
</template>

<script>
export default {
    methods: {
        openUserGuide() {
            let user = this.$store.state.user

            if (user.role == "STUDENT") {
                window.open("https://trello.com/b/8p1Gtdi8/program-backlog")
            } else if (user.role == "EMPLOYER") {
                window.open("https://www.youtube.com/watch?v=myng3YOT7JY&ab_channel=ProjectMons")
            } else if (user.role == "ADMIN") {
                window.open("https://www.youtube.com/watch?v=dQw4w9WgXcQ&ab_channel=RickAstley")
            }
        }
    },
    mounted() {
    },
}
</script>