<template>
  <main>
    <v-card>
      <v-card-title>
        <v-text-field
          v-model="employerApplicationSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="employerApplicationHeaders"
        :items="employerApplications"
        :search="employerApplicationSearch"
        :loading="employerApplicationLoading"
        loading-text="Loading... Please wait"
      >
        <template v-slot:[`item.actions`]="{ item }">
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                class="mr-2"
                @click="showEmployerApplicationDetails(item)"
                text
                icon
                v-bind="attrs"
                v-on="on"
              >
                <v-icon small> mdi-magnify </v-icon>
              </v-btn>
            </template>
            <span>Application Details</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                class="mr-2"
                @click="confirmApprove(item)"
                text
                icon
                v-bind="attrs"
                v-on="on"
              >
                <v-icon small> mdi-check </v-icon>
              </v-btn>
            </template>
            <span>Approve Application</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                class="mr-2"
                @click="confirmReject(item)"
                text
                icon
                v-bind="attrs"
                v-on="on"
              >
                <v-icon small> mdi-close </v-icon>
              </v-btn>
            </template>
            <span>Reject Application</span>
          </v-tooltip>
        </template>
      </v-data-table>
    </v-card>
    <v-dialog v-model="approveConfirmationDialog" persistent width="500">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Approve Application
        </v-card-title>
        <v-card-text>
          Are you sure you want to approve this application?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red lighten-3"
            text
            @click="approveConfirmationDialog = false"
          >
            Cancel
          </v-btn>
          <v-btn color="primary" text @click="approve()"> Confirm </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="rejectConfirmationDialog" persistent width="500">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Reject Application
        </v-card-title>
        <v-card-text class="text-left">
          <p>Are you sure you want to reject this application?</p>
          <validation-observer ref="observer">
            <validation-provider
              v-slot="{ errors }"
              name="Reject Feedback"
              rules="required"
            >
              <v-text-field
                v-model="feedback"
                label="Reject Reason"
                outlined
                dense
                color="#2C4C9C"
                autocomplete="false"
                required
                :counter="100"
                :error-messages="errors"
              />
            </validation-provider>
          </validation-observer>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red lighten-3"
            text
            @click="rejectConfirmationDialog = false; $refs.observer.reset();"
          >
            Cancel
          </v-btn>
          <v-btn color="primary" text @click="reject()"> Confirm </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="applicationDetailDialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <EmployerApplicationDetailDialog
        :employerApplication="currentEmployerApplication"
        @close="applicationDetailDialog = false"
      ></EmployerApplicationDetailDialog>
    </v-dialog>
    <v-snackbar
      v-model="notification"
      timeout="1000"
      :color="notiColor"
      v-bind="{
        bottom: true,
        right: true,
      }"
    >
      {{ notificationText }}

      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="notification = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </main>
</template>
<script>
import { axiosConfig } from "../../axiosConfig";
import EmployerApplicationDetailDialog from ".././EmployerApplicationDetailDialog.vue";
import { required } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("lazy");

extend("required", {
  ...required,
  message: "{_field_} cannot be empty",
});

export default {
  name: "MyJobList",
  components: {
    EmployerApplicationDetailDialog,
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      employerApplicationHeaders: [
        {
          text: "Employer Name",
          align: "start",
          filterable: true,
          value: "employerName",
        },
        {
          text: "Employer Email",
          align: "start",
          value: "employerEmail",
        },
        {
          text: "Employer Industry",
          align: "start",
          value: "employerIndustry",
        },
        {
          text: "Co-Admin Approval",
          align: "start",
          value: "coAdminApproval",
        },
        {
          text: "Actions",
          align: "center",
          filterable: false,
          sortable: false,
          value: "actions",
        },
      ],
      employerApplications: [],
      employerApplicationSearch: null,
      employerApplicationLoading: true,
      applicationDetailDialog: false,
      currentEmployerApplication: null,
      axiosInstance: null,
      feedback: null,
      notification: false, // true will show notification snackbar
      notiColor: "#19946b", // determine the color for the notification snackbar
      notificationText: null, // content of the notification snackbar
      approveConfirmationDialog: false,
      rejectConfirmationDialog: false,
    };
  },
  /* eslint-disable */
  /* Disable error logging for this file */
  methods: {
    // Function to display message to the user
    displayNotification(message, color) {
      this.notificationText = message;
      this.notification = true;
      this.notiColor = color;
    },
    confirmApprove(employerApplication) {
      this.currentEmployerApplication = employerApplication;
      this.approveConfirmationDialog = true;
    },
    confirmReject(employerApplication) {
      this.currentEmployerApplication = employerApplication;
      this.rejectConfirmationDialog = true;
    },
    async approve() {
      // todo: modify in database
      // Modify route based on role (COADMIN/ADMIN)
    
      let role = this.$store.state.user.role.toLowerCase();
      if(role == 'admin')
      {
        await this.axiosInstance
        .put(
          process.env.VUE_APP_BACKEND_LINK+`admin/app/adapprove/${this.currentEmployerApplication._id}`
        )
        .then((res) => {
          // TODO: Should remove the employerApplication from the list
          this.displayNotification(
            "EMPLOYER APPLICATION APPROVED SUCCESSFULLY",
            "#19946b"
          );
          this.showEmployerApplication();
        })
        .catch((err) => {
          this.displayNotification(
            "ERROR APPROVING EMPLOYER APPLICATION",
            "#94193c"
          );
        });
      }
      else{
        await this.axiosInstance
        .put(
          process.env.VUE_APP_BACKEND_LINK+`coadmin/app/coapprove/${this.currentEmployerApplication._id}`
        )
        .then((res) => {
          this.displayNotification(
            "EMPLOYER APPLICATION APPROVED BY CO-ADMIN SUCCESSFULLY",
            "#19946b"
          );
          this.showEmployerApplication();
        })
        .catch((err) => {
          this.displayNotification(
            "ERROR APPROVING EMPLOYER APPLICATION BY CO-ADMIN",
            "#94193c"
          );
        });
      }
      this.approveConfirmationDialog = false;

    },
    async reject() {
      // todo:  modify in database
      // Modify route based on role (COADMIN/ADMIN)
      let role = this.$store.state.user.role.toLowerCase();
      if(role == 'admin')
      {
        await this.axiosInstance
        .put(
          process.env.VUE_APP_BACKEND_LINK+`admin/app/reject/${this.currentEmployerApplication._id}`,
          { feedback: this.feedback }
        )
        .then((res) => {
          this.displayNotification(
            "EMPLOYER APPLICATION REJECTED SUCCESSFULLY",
            "#19946b"
          );
          this.showEmployerApplication();
        })
        .catch((err) => {
          this.displayNotification(
            "ERROR REJECTING EMPLOYER APPLICATION",
            "#94193c"
          );
        });
      }
      else{
        await this.axiosInstance
        .put(
          process.env.VUE_APP_BACKEND_LINK+`coadmin/app/reject/${this.currentEmployerApplication._id}`,
          { feedback: this.feedback }
        )
        .then((res) => {
          this.displayNotification(
            "EMPLOYER APPLICATION REJECTED SUCCESSFULLY BY CO-ADMIN",
            "#19946b" 
          );
          this.showEmployerApplication();
        })
        .catch((err) => {
          this.displayNotification(
            "ERROR REJECTING EMPLOYER APPLICATION BY CO-ADMIN",
            "#94193c"
          );
        });
      }
      
      this.rejectConfirmationDialog = false;
    },
    showEmployerApplicationDetails(item) {
      this.currentEmployerApplication = item;
      this.applicationDetailDialog = true;
    },
    async showEmployerApplication() {
      this.employerApplicationLoading = true;
      await this.axiosInstance
        .get(process.env.VUE_APP_BACKEND_LINK+"admin/app/pendingapp")
        .then((res) => {
          this.employerApplications = res.data;
          this.employerApplicationLoading = false;
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
          this.displayNotification(
            "ERROR SHOWING EMPLOYER APPLICATIONS",
            "#94193c"
          );
        });
    },
  },
  async created() {
    this.axiosInstance = axiosConfig(
      this.$store.state.user.token,
      this.$store.state.user.role.toLowerCase()
    );
    this.showEmployerApplication();
  },
};
</script>
