<style>
@import "../../assets/styles/home-page.css";
</style>

<template>
  <v-app id="inspire">

    <main>
      <!-- Loading Icon while frontend requesting for data -->
      <v-dialog v-model="dialog" hide-overlay persistent width="300">
        <v-card color="primary" dark>
          <v-card-text>
            Loading...
            <v-progress-linear
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-img
        max-height="350"
        src="https://api.time.com/wp-content/uploads/2021/02/laptop-home-office.jpg"
      >
        <v-container 
          class="pt-16" 
          max-height="300"
          >
          <v-card class="mx-auto mt-16 justify-center" max-width="1000" :align="center">
            <v-toolbar 
              elevation="4"
              outlined
              shaped>
              <v-text-field    
                v-show="!typeWriterFlag"
                id="searchBar"
                hide-details
                dense
                active
                v-model="searchJobAdvertKeyword"
                class="mr-4"
                @keydown.enter="searchJobAdvert()"
              ></v-text-field>
              <!-- <v-text-field
                v-show="typeWriterFlag"    
                id="searchBarWithTypeWriter"
                hide-details
                dense
                v-model="searchJobAdvertKeyword"
                class="mr-4"
                @click="stopTypeWriter"
                @keydown.enter="searchJobAdvert()"
              ></v-text-field> -->
              <v-select
                hide-details
                dense
                :items="jobTypes"
                label="Job Type"
                v-model="selectedJobType"
              ></v-select>

              <v-btn icon @click="searchJobAdvert">
                <v-icon>mdi-magnify</v-icon>
              </v-btn>

              <v-btn icon @click="clearSearch()">
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar>
          </v-card>
        </v-container>
      </v-img>

      <v-container fluid class="blue darken-4" dark>
        <v-row>
          <v-col
            cols="12"
            sm="12"
            md="6"
            lg="6"
            xl="4"
            v-for="job in jobAdverts"
            :key="job._id"
          >
            <JobCard :job="job" />
          </v-col>
          <v-col cols="12">
            <v-pagination
              v-model="page"
              class="my-4"
              :total-visible="7"
              :length="pageSize"
              @input="onPageChange"
            ></v-pagination>
          </v-col>
        </v-row>
      </v-container>
      <FooterCard></FooterCard>
      <v-snackbar
        v-model="notification"
        timeout="1000"
        :color="notiColor"
        v-bind="{
          bottom: true,
          right: true,
        }"
      >
        {{ notificationText }}

        <template v-slot:action="{ attrs }">
          <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="notification = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </main>
  </v-app>
</template>

<script>
import FooterCard from "../FooterCard.vue";
import JobCard from "../JobCard.vue";
import { axiosConfig } from "../../axiosConfig";
import store from "../../store";
import axios from "axios";

export default {
  components: { FooterCard, JobCard: JobCard },
  data: () => ({
    drawer: null,
    items: [
      { title: "Dashboard", icon: "mdi-view-dashboard" },
      { title: "Jobs", icon: "mdi-image" },
      { title: "Employer", icon: "mdi-help-box" },
    ],
    expand: false,
    expand2: false,
    searchJobAdvertKeyword: "",
    jobAdverts: [],
    selection: "",
    selectedCourse() {},
    select: null,
    icons: ["mdi-facebook", "mdi-twitter", "mdi-linkedin", "mdi-instagram"],
    reqPath: "",
    page: 1,
    pageSize: 0,
    dialog: true,
    axiosInstance: null,
    jobTypes: ["", "VERIFIED", "NOT VERIFIED"],
    selectedJobType: "",
    notification: false, // true will show notification snackbar
    notiColor: "#19946b", // determine the color for the notification snackbar
    notificationText: null, // content of the notification snackbar
  }),
  async mounted() {
    let userData;

    //get the token from backend
    await axios
      .get(process.env.VUE_APP_BACKEND_LINK+"visitor/users/resolve", {
        withCredentials: true,
      })
      .then((res) => {
        if (res.data.length === 0) {
          //User is redirected to signon page to signin again due to token being expired.
          this.$router.push({ name: "signup" });
        } else {
          userData = res.data;
          //Setting important data in vuex global store
          this.$store.state.user.auth = true;
          this.$store.state.user.token = userData.token;
          this.$store.state.user.email = userData.user.email;
          this.$store.state.user.firstName = userData.user.given_name;
          this.$store.state.user.lastName = userData.user.family_name;
          this.$store.state.user.profilePic =
            userData.userDb[0].image != null
              ? userData.userDb[0].image
              : userData.user.picture;
          this.$store.state.user.role = userData.userDb[0].userRole;
        }
      })
      .catch((err) => {
        console.log(err);
        this.$router.push({ name: "signup" });
      });

    this.axiosInstance = axiosConfig(
      store.state.user.token,
      store.state.user.role.toLowerCase()
    );

    let data = {
      keyword: "",
      jobType: "",
    };

    this.reqPath = this.getRoleRoute("job/search");
    await this.axiosInstance
      .post(this.reqPath, data)
      .then((response) => {
        this.jobAdverts = response.data[0];
        this.pageSize = response.data[1];
        console.log(response);
      })
      .catch((err) => {
        console.log(err);
        this.displayNotification(
          "ERROR RETRIEVING JOB ADVERTISEMENTS",
          "#94193c"
        );
      });
    this.dialog = false;

    //Placeholder Text HERE
    let text_list = [
          "Data Science",
          "Software Engineer",
          "Civil Engineer",
          "Cyber Security",
          "Mechatronics Engineer",
          "Chemical Engineer"
      ];
    
    this.typeWriter("#searchBarWithTypeWriter", text_list, true);
  },
  methods: {
    // Function to display message to the user
    displayNotification(message, color) {
      this.notificationText = message;
      this.notification = true;
      this.notiColor = color;
    },
    routeToApplication(jobId) {
      this.$router.push({
        name: "application",
        params: { selectedJob: jobId },
      });
    },
    searchJobAdvert() {
      this.dialog = true;
      this.axiosInstance
        .post(this.reqPath, {
          keyword: this.searchJobAdvertKeyword,
          jobType: this.mapJobType(this.selectedJobType),
        })
        .then((response) => {
          this.jobAdverts = response.data[0];
          this.pageSize = response.data[1];
          this.dialog = false;
        });
    },
    clearSearch() {
      this.dialog = true;
      this.axiosInstance
        .post(this.reqPath, {
          keyword: "",
          jobType: "",
        })
        .then((response) => {
          this.jobAdverts = response.data[0];
          this.pageSize = response.data[1];
          this.dialog = false;
        });
      this.searchJobAdvertKeyword = null;
      this.selectedJobType = "";
    },
    getRoleRoute(actionPath) {
      let linkHeader = process.env.VUE_APP_BACKEND_LINK;
      if (this.$store.state.user.role.localeCompare("EMPLOYER") == 0) {
        return linkHeader + "emp/" + actionPath;
      } else if (this.$store.state.user.role.localeCompare("STUDENT") == 0) {
        return linkHeader + "stud/" + actionPath;
      } else if (this.$store.state.user.role.localeCompare("ADMIN") == 0) {
        return linkHeader + "admin/" + actionPath;
      } else {
        console.log("ERROR ROLE: " + this.$store.state.user.role);
      }
    },
    onPageChange(newPage) {
      this.axiosInstance
        .post(this.reqPath, {
          keyword: this.searchJobAdvertKeyword,
          jobType: this.mapJobType(this.selectedJobType),
          page: newPage,
        })
        .then((response) => {
          this.page = newPage;
          this.jobAdverts = response.data[0];
        });
    },

    typeWriter(selector_target, text_list, placeholder = false, i = 0, text_list_i=0, delay_ms=100) {
        if (!i) {
            if (placeholder) {
                document.querySelector(selector_target).placeholder = "";
            }
            else {
                document.querySelector(selector_target).innerHTML = "";
                return
            }
        }
        var txt = text_list[text_list_i];
        if (i < txt.length) {
            if (placeholder) {
                document.querySelector(selector_target).placeholder += txt.charAt(i);
            }
            else {
                document.querySelector(selector_target).innerHTML += txt.charAt(i);
            }
            i++;
            setTimeout(this.typeWriter, delay_ms, selector_target, text_list, placeholder, i, text_list_i);
        }
        else {
            text_list_i++;
            if (typeof text_list[text_list_i] === "undefined")  {
                setTimeout(this.typeWriter, (delay_ms*5), selector_target, text_list, placeholder);
            }
            else {
                i = 0;
                setTimeout(this.typeWriter, (delay_ms*3), selector_target, text_list, placeholder, i, text_list_i);
            }
        }
    },
    stopTypeWriter() {
      this.typeWriterFlag = false
    },
    startTypeWriter() {
      this.typeWriterFlag = true
    },
    mapJobType(jobType) {
      if (jobType == "VERIFIED") {
        return "COMPULSORY";
      } else if (jobType == "NOT VERIFIED") {
        return "VOLUNTARY";
      } else {
        return "";
      }
    },
  },
};
</script>
<style></style>
